import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateConfigService } from '../../services/translate-config.service';

@Component({
  selector: 'app-newsroom',
  templateUrl: './newsroom.page.html',
  styleUrls: ['./newsroom.page.scss'],
})
export class NewsroomPage implements OnInit {

  selectedLanguage:string;

  constructor(private translateConfigService: TranslateConfigService, private router: Router) { 
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();

    this.translateConfigService.setLanguage('en');
  }

  ngOnInit() {
  }

  navToNews() {
    this.router.navigate(['/news-list']);
  }

  navToMediaRelease() {
    this.router.navigate(['/media-release']);
  }

  navToPhotoGallery() {
    this.router.navigate(['/photo-gallery']);
    
  }

}
